.popup-thanks {
	max-width: 600px;
	width: 100%;
	padding: 50px;
	border: 1px solid $orange;

	&__title {
		font-size: 42px;
		line-height: 50px;
		margin-bottom: 30px;
		text-align: center;
	}

	&__text {
		font-size: 20px;
		line-height: 130%;
		color: $gray;
		text-align: center;
	}
}